//-----------------------------------------------//
//--------------------Typo-----------------------//
//-----------------------------------------------//
@import url('https://fonts.googleapis.com/css2?family=Golos+Text:wght@400;600&family=Inconsolata:wght@200;300;400;600;700&family=Open+Sans:wght@300;500;600;700&display=swap');
$f1: 'Golos Text', sans-serif;
$f2: 'Inconsolata', monospace;
$f3: 'Open Sans', sans-serif;



//-----------------------------------------------//
//-------------------Global----------------------//
//-----------------------------------------------//
*{
  margin: 0;
  padding: 0;
}
body{
  background-color: #141414;
  &.hide{
    content: "";
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    background-color: rgba(0, 0, 0, 0.384);
    z-index: 3;
  }
}
img{
  max-width: 100%;
}
li{
  list-style-type: none;
}
a{
  text-decoration: none;
  color: inherit;
}
.main{
  margin-top: -80px;
  position: relative;
  z-index: 6;
}



//-----------------------------------------------//
//-------------------Swipers---------------------//
//-----------------------------------------------//
.swiper{
  // max-width: 1900px;
  margin: 0px auto;
  padding: 0 50px;
}
.swiper__img{
  overflow: hidden;
  border-radius: 5px;
  position: relative;
  width: calc(100% / 6);
  max-height: 392px;
  // max-width: 308px;
  & > p{
    position: absolute;
    color: #e9e8e8;
    bottom: 0;
    left: 50%;
    width: 100%;
    text-align: center;
    transform: translate(-50%,-50%);
    font-family: $f1;
    z-index: 2;
    font-size: 12px;
    white-space: nowrap;
    background-color: #141414b0;
    border: #DD0D18 1px solid;
    padding: 10px 16px;
    border-radius: 5px;
    opacity: 0.8;
  }
  &:hover{
    & > p{
      opacity: 1;
    }
  }
  &::after{
    content: "";
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    background-color: #00000044;
  }
  cursor: pointer;
  img{
    width: 100%;
    object-fit: cover;
    object-position: center;
    display: block;
  }
}
.swiper-slide{
  display: flex;
  justify-content: center;
  gap: 7px;
}
.swiper-button-prev,.swiper-button-next{
  color: #DD0D18;
  transition: .2s all ease-in-out;
  &:hover{
    color: #d8d8d8;
  }
}



//-----------------------------------------------//
//-------------------Sections--------------------//
//-----------------------------------------------//
.popular,.latest{
  position: relative;
  background-color: #141414;
}
.top_rated .sectionMain__title{
  background-color: transparent;
}
.top_rated .wrapper-swiper{
  background-color: #141414;
  padding-bottom: 100px;
}
.main section{
  font-family: $f3;
  h3{
    padding-left: 10px;
    color: #e9e8e8;
    font-size: 26px;
    font-weight: 600;
  }
}
.main section:not(:first-child){
  padding-bottom: 100px;
}
.sectionMain__title{
  display: flex;
  // margin: 0 auto 15px;
  align-items: center;
  justify-content: flex-start;
  gap: 15px;
  margin-bottom: 15px;
  width: 410px;
  cursor: pointer;
  // width: auto;
  &:hover .exploreHidden > div{
    transform: translateX(-0px);
    color: #54b9c5;
  }
  .exploreHidden{
    overflow: hidden;
    color: #e9e8e8;
    cursor: pointer;
    & > div{
      display: flex;
      align-items: center;
      justify-content: flex-start;
      gap: 8px;
      transform: translateX(-110px);
      transition: .3s all ease-in-out;
    }
  }
}

.footer{
  color: #d8d8d8;
  display: flex;
  justify-content: center;
  padding: 50px 0px;
  font-style: italic;
  font-family: $f2;
  font-size: 14px;
  small a{
    color: #DD0D18;
    &:hover{
      color: lighten($color: #DD0D18, $amount: 20);
      border-bottom: 1px solid lighten($color: #DD0D18, $amount: 20);
    }
  }
}