//-----------------------------------------------//
//--------------------Typo-----------------------//
//-----------------------------------------------//
@import url('https://fonts.googleapis.com/css2?family=Golos+Text:wght@400;600&family=Inconsolata:wght@200;300;400;600;700&family=Open+Sans:wght@300;500;600;700&display=swap');
$f1: 'Golos Text', sans-serif;
$f2: 'Inconsolata', monospace;
$f3: 'Open Sans', sans-serif;
//-----------------------------------------------//
//--------------------Navbar---------------------//
//-----------------------------------------------//
.header{
  min-height: 100vh;
  background: url(https://occ-0-767-1335.1.nflxso.net/dnm/api/v6/6AYY37jfdO6hpXcMjf9Yu5cnmO0/AAAABdJicBzWcOnu065IkV1BZA-6Ik3NEH-6lAqQXoilrVDfZPu7pWjI0sUZMUmc7XhLf_u5tHXkX71OArTK8PYgNighSa6d4P6_xO-d.webp?r=8ae);
  background-size: cover;
  position: relative;
  &::after{
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: linear-gradient(180deg, rgba(47,90,128,0) 0%, rgba(20,20,20,1) 100%);
  }
}

.nav{
  position: fixed;
  z-index: 8;
  top: 0;
  left: 0;
  width: 100%;
  padding: 0 60px;
  background: linear-gradient(0deg, rgba(47,90,128,0) 0%, rgba(20,20,20,1) 99%);
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: 68px;
  font-family: $f1;
  font-size: 14px;
  transition: .3s all ease-in-out;
  &.bgBlack{
    background-color: black;
  }
}
// Nav Left
.nav__left{
  display: flex;
  align-items: center;
  .btnMobile{
    display: none;
  }
  &__logo{
    width: 92.5px;
    margin-right: 25px;
    cursor: pointer;
  }
  &__links{
    display: flex;
    align-items: center;
    color: #e9e8e8;
    li{
      margin-left: 20px;
      transition: .2s all ease-in-out;
    }
    li:first-child{
      font-weight: 700;
    }
    li:not(:first-child){
      cursor: pointer;
      &:hover{
        color: #c0c0c0;
      }
    }
  }
}
// Nav Right
.nav__right{
  display: flex;
  align-items: center;
  color: #e9e8e8;
  div:nth-of-type(1),div:nth-of-type(2),div:nth-of-type(3){
    margin-right: 15px;
    transition: .3s all ease-in-out;
    &:hover{
      color: #c0c0c0;
    }
  }
  &__account{
    position: relative;
    .logo{
      display: flex;
      align-items: center;
      gap: 10px;
      i{
        transition: .2s all ease-in;
      }
      &.active{
        i{
          transform: rotate(180deg);
        }
      }
    }
  }
}




//-----------------------------------------------//
//-------------------Searchbar-------------------//
//-----------------------------------------------//
.searchHidden{
  overflow: hidden;
}
.nav__right__searchbar{
  display: flex;
  align-items: center;
  position: relative;
  overflow: hidden;
  transform: translateX(250px);
  &.active{
    transform: translateX(0px);
    input{
      background-color: rgba(0, 0, 0, 0.562);
      border: 1px solid #e9e8e8;
    }
  }
  cursor: pointer;
  input{
    position: relative;
    height: 35px;
    width: 270px;
    background-color: transparent;
    color: #e9e8e8;
    outline: none;
    border: none;
    padding: 0 40px;
    transition: .3s all ease-in-out;
    &::placeholder{
      color: gray;
    }
  }
  svg{
    transition: .3s all ease-in-out;
    outline: none;
    transform: translateX(10px);
    position: absolute;
    z-index: 2;
  }
}
.nav__right__bell,.Jeunesse,.nav__right__account .logo{
  cursor: pointer;
}




//-----------------------------------------------//
//-----------------Account Menu------------------//
//-----------------------------------------------//
.extentedMenu{
  width: 300px;
  position: absolute;
  top: 40px;
  left: -250px;
  background-color: rgba(0, 0, 0, 0.685);
  border: 1px solid #e9e8e8;
  color: #e9e8e8;
  font-size: 13px;
  display: flex;
  flex-direction: column;
  gap: 20px;
  padding-top: 30px;
  display: none;
  & > *{
    display: flex;
    align-items: center;
    gap: 20px;
    transition: .2s all ease-in-out;
    &:hover{
      color: #c0c0c0;
    }
  }
  a{
    color: #e9e8e8;
    text-decoration: none;
  }
  .line{
    height: 1px;
    width: 100%;
    background-color: #e9e8e8;
    margin-top: 20px;
  }
  a:last-child{
    align-self: center;
    padding-top: 30px;
    padding-bottom: 30px;
  }
  a:not(:last-child){
    padding-left: 40px;
  }
  
  transition: .3s all ease-in-out;
  &.active{
    display: flex;
  }
}




//-----------------------------------------------//
//--------------Random Movie---------------------//
//-----------------------------------------------//
.header{
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.headerTitleContainer{
  display: flex;
  flex-direction: column;
  gap: 20px;
  padding-left: 100px;
  font-family: $f1;
  z-index: 2;
  & > p{
    color: #e9e8e8;
  }
  h2{
    font-size: 90px;
    color: #fff;
  }
  p{
    max-width: 50ch;
    line-height: 30px;
  }
}
.btnsContainer{
  display: flex;
  gap: 30px;
  font-size: 22px;
  font-weight: 700;
  font-family: $f3;
  p{
    padding: 15px 40px;
    border-radius: 5px;
    display: flex;
    align-items: center;
    gap: 20px;
    transition: .1s all ease-in-out;
    cursor: pointer;
    i{
      font-size: 26px;
    }
  }
  p:nth-of-type(1){
    background-color: #fff;
    &:hover{
      background-color: darken($color: #fff, $amount: 30);
    }
  }
  p:nth-of-type(2){
    background-color: rgba(128, 128, 128, 0.644);
    color: #e9e8e8;
    &:hover{
      background-color: rgba(128, 128, 128, 0.315); 
    }
  }
}

//-----------------------------------------------//
//-------------Modal----------------//
//-----------------------------------------------//
.randomModal{
  color: #e9e8e8;
  font-family: $f1;
  position: fixed;
  left: 50%;
  top: 50px;
  background-color: #181818;
  border-radius: 15px;
  overflow: hidden;
  width: 920px;
  z-index: -1;
  transition: .15s all ease-in;
  // transition: 0.8s all cubic-bezier(0.68, -0.55, 0.265, 1.55);
  transform: translate(-50%, 0%) scale(.9);
  opacity: 0;
  &.open{
    // z-index: 10;
    transition: .15s opacity ease-in-out .3s;
    opacity: 1;
    transform: translate(-50%, 0%) scale(1);
  }
  &__head{
    min-height: 50vh;
    // background: url(https://occ-0-767-1335.1.nflxso.net/dnm/api/v6/6AYY37jfdO6hpXcMjf9Yu5cnmO0/AAAABdJicBzWcOnu065IkV1BZA-6Ik3NEH-6lAqQXoilrVDfZPu7pWjI0sUZMUmc7XhLf_u5tHXkX71OArTK8PYgNighSa6d4P6_xO-d.webp?r=8ae);
    background-position: top;
    background-size: cover;
    background-repeat: no-repeat;
    position: relative;
    &:before{
      content: "";
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background-color: #18181852;
    }
    iframe{
      z-index: -1;
      width: 100%;
      height: 50vh;
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%,-50%);
      display: block;
    }
    &::after{
      content: "";
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background: linear-gradient(180deg, rgba(2,0,36,0) 92%, rgba(24,24,24,1) 100%);
    }
    .closeBtnModal{
    position: absolute;
    right: 0.5%;
    top: 1%;
    font-size: 30px;
    z-index: 10;
    transition: .2s all ease-in-out;
    cursor: pointer;
    &:hover{
      color: darken($color: #e9e8e8, $amount: 30);
    }
    }
    
  }
}
// Header
.randHeadContainer{
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding-left: 50px;
  height: 50vh;
  position: relative;
  z-index: 8;
  h3{
    font-size: 90px;
    margin-bottom: 50px;
    color: #fff;
  }
}
.randomModal__head__Btns{
  display: flex;
  align-items: center;
  justify-content: flex-start;
  gap: 15px;
  p{
    transition: .2s all ease-in-out;
    cursor: pointer;
  }
  p:nth-of-type(1){
    padding: 10px 40px;
    border-radius: 5px;
    background-color: #fff;
    cursor: pointer;
    color: #181818;
    font-weight: 500;
    font-size: 22px;
    &:hover{
      background-color: darken($color: #fff, $amount: 30);
    }
  }
  p:nth-of-type(2){
    font-size: 30px;
    font-weight: 300;
    border: 2px solid #959392b2;
    border-radius: 50%;
    padding: 0px 8px;
    &:hover{
      border: 2px solid rgba(255, 254, 254, 0.698);
    }
  }
  p:nth-of-type(3){
    font-size: 20px;
    font-weight: 300;
    border: 2px solid #959392b2;
    border-radius: 50%;
    padding: 6px 8px;
    &:hover{
      border: 2px solid rgba(255, 254, 254, 0.698);
    }
  }
}
// COntent
.randomModal__content{
  line-height: 22px;
  padding: 20px 20px 50px;
  display: grid;
  grid-template-columns: 3fr 1fr;
  gap: 20px;
  
  .randomModalsmallInfos{
    display: flex;
    align-items: center;
    gap: 20px;
    div:first-child{
      font-weight: 700;
      display: flex;
      gap: 5px;
    }
  }
  .randomModalCol:first-child{
    display: flex;
    flex-direction: column;
    gap: 50px;
    .randomModalOverview{
      font-size: 13px;
      width: 60ch;
    }
  }
  .randomModalCol:last-child{
    font-size: 12px;
    & > div{
      display: flex;
      justify-content: flex-start;
      gap: 10px;
      & > span{
        color: #b8b8b8;
      }
    }
  }
}
.modalCreatorText{
  color: #b8b8b8;
  span:nth-child(even){
    color: #DD0D18;
  }
  span:nth-child(odd){
    color: #ffffffd7;
  }
}
.modalGenreText{
  color: #b8b8b8;
  span:nth-child(even){
    color: #DD0D18;
  }
  span:nth-child(odd){
    color: #ffffffd7;
  }
}